.card-container {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    transform: translate(0);
    transition: transform 0.25 ease-out;


}

.card-container:hover{
    transform: scale(1.05);
}